.container2 {
    width: 450px;
    height: 300px;
    position: absolute;
    border-radius: 100px;
 }
 
 .container2 img {
    width: 100%;
    height: 100%;
 }
 
 .container2:before {
   position: absolute;
   top: 0;
   left: 0;
   height: 100%;
   width: 100%;
   content: '';
   background-color: #2d3436;
   opacity: 0.8;
 }
 
 .caption2 {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: white;
    text-transform: uppercase;
 }
 
 .container2 .caption2 h2 {
    text-align: center;
    font-size: 17px;
    padding: 20px;
    margin-top: 20px;
    background: rgba(0, 0, 0, 0.8);
 }
 
 .container2 .caption2 p {
    font-size: 16px;
    width: 80%;
    align-self: center;
    text-align: center;
 }
 
 .container2 .caption2 a {
    text-decoration: none;
    background: #000;
    color: #fff;
    width: 89px;
    padding: 15px;
    margin-bottom: 20px;
    align-self: center;
  }

  .container2 .caption2 a.disabled{
    cursor: default;
    pointer-events: none;
  }
 
 /* EFFECTS STYLES GOES HERE */
 .container2 {
   background-color: #444;
   overflow: hidden;
   /* border: 10px solid white; */
 }
 
 .container2 img {
   transition: all 0.5s;
   transition-delay: 0.5s;
 }
 
 .container2:hover img {
   transform: scale(0);
   transition-delay: 0s;
 }
 
 .container2:before {
   background-color: #2876a5;
   transform: scale(0) rotate(-180deg);
   transition: all 0.5s;
 }
 
 .container2:hover:before {
   transform: scale(1) rotate(0deg);
   transition-delay: 0.5s;
 }
 
 .container2 .caption2 h2, .p-caption2, .a-caption{
   opacity: 0 ;
   transition: all 0.5s ;
 }
 
 .container2:hover h2,
 .container2:hover p,
 .container2:hover a {
   opacity: 1;
   transition-delay: 1s;
 }