.container3 {
    width: 450px;
    height: 300px;
    position: relative;
    border-radius: 5px;
 }
 
 .container3 img {
    width: 100%;
    height: 100%;
 }
 
 .container3:before {
   position: absolute;
   top: 0;
   left: 0;
   height: 100%;
   width: 100%;
   content: '';
   background-color: #2d3436;
   opacity: 0.8;
 }
 
 .caption3 {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: white;
    text-transform: uppercase;
 }
 
 .container3 .caption3 h2 {
    text-align: center;
    font-size: 17px;
    padding: 20px;
    margin-top: 20px;
    background: rgba(0, 0, 0, 0.8);
 }
 
 .container3 .caption3 p {
    font-size: 16px;
    width: 80%;
    align-self: center;
    text-align: center;
 }
 
 .container3 .caption3 a {
    text-decoration: none;
    background: #000;
    color: #fff;
    width: 89px;
    padding: 15px;
    margin-bottom: 20px;
    align-self: center;
  }
 
 /* hovering effect goes here */
 .container3 {
   background-color: #444;
   /* border: 10px solid white;  */
   overflow: hidden;
 }
 
 .container3 .caption3 img {
   transition: all 0.5s;
 }
 
 .container3:hover img {
   transform: rotate(720deg) scale(0);
   opacity: 0;
 }
 
 .container3:before {
   background-color: #2876a5;
   opacity: 0;
   transition: all 0.3s; 
 }
 
 .container3:hover:before {
   opacity: 1;
   transition-delay: 0.5s;
 }
 
 .container3 .caption3 a {
   transform: translateY(-300px);
   transition: all 0.2s;
 }
 
 .container3:hover a {
   transform: translateY(0);
   transition-delay: 0.5s;
 }
 
 .container3 .caption3 p {
   transform: translateY(-300px);
   transition: all 0.2s;
 }
 
 .container3:hover .caption3 p {
   transform: translateY(0);
   transition-delay: 0.7s;
 }
 
 .container3 .caption3 h2 {
   transform: translateY(-300px);
   transition: all 0.2s;
 }
 
 .container3:hover .caption3 h2 {
   transform: translateY(0);
   transition-delay: 0.9s;
 }