
  .buttonidea2 {
      text-decoration: none;
      text-transform: uppercase;
      font-size: 20px;
	    font-family: sans-serif;
      color: white;
      letter-spacing: 3px;
      padding: 20px 40px;
      position: relative;
      border: 3px solid black ;
      transition: all 0.2s;
      overflow: hidden;
      letter-spacing: 2px;
      /* background-color: #2876a5; */
      /* z-index: -1; */
    }
    
    .buttonidea2 .btn-idea2{
      font-family: sans-serif;
      font-size: 20px;

    }

    .buttonidea2:before,
    .buttonidea2:after,
    .btn-idea2:before,
    .btn-idea2:after {
      content: "";
      position: absolute;
      width: 25%;
      height: 100%;
      background-color: #87b83d;
      z-index: -1;
      transition: all 0.1s;
    }
    
    .buttonidea2:before {
      top: 100%;
      left: 0%;
    }
    
    .buttonidea2:hover:before {
      top: 0;
      left: 0;
    }
    
    .buttonidea2:after {
      top: -100%;
      right: 0;
      transition-delay: 0.3s
    }
    
    .buttonidea2:hover:after {
      top: 0;
      right: 0;
    }
    
    .buttonidea2 span:before {
      top: -100%;
      left: 25%;
      transition-delay: 0.1s;
    }
    
    .buttonidea2:hover span:before {
      top: 0;
      left: 25%;
    }
    
    .buttonidea2 span:after {
      bottom: -100%;
      right: 25%;
      transition-delay: 0.2s
    }
    
    .buttonidea2:hover span:after {
      bottom: 0;
      right: 25%;
    }
    
    .buttonidea2:hover {
      color: black;
    }