.headbar {
  top: 0;
  left: 0;
  position: fixed;
  height: 57px;
  width: 100%;
  background-color: #2876a5;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 5%), 0 1px 5px 0 rgba(0, 0, 0, 5%);
}

.headerContainer {
  color: white;
  display: flex;
  justify-content: space-between;
}

.menu {
  display: flex;
  justify-content: space-between;
  width: 15rem;
  margin-right: 2rem;
  font-weight: 500 ;
}

.menu p a{
  color: white;
  text-decoration: none ;
}


.menu p a:hover,
.menu p a:active,
.menu p a.active{
  color: white;
  padding-bottom: 0.25rem;
  border-bottom: 4px solid white;
  /* text-shadow: 0px 1px 0px rgba(255,255,255,.3), 0px -1px 0px rgba(0,0,0,.7); */
}


.headerContainer .menuDescription a{
    text-decoration: none;
    color:white;
    margin-left: 1.5rem;
    /* usefull for engraving the text */
    text-shadow: 0px 1px 0px rgba(255,255,255,.3), 0px -1px 0px rgba(0,0,0,.7);
}
