@import url('http://fonts.cdnfonts.com/css/best-signature-font');


@font-face {
    font-family: signatureAlb0;
    src: url('../../assets/fonts/AutografPersonalUseOnly-mOBm.ttf');
}

.welcome-container{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 60%;
}

.welcome-container h1{
    color: #ffffffc9;
    font-family: sans-serif;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9,
    0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1),
    0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2),
    0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2),
    0 7px 7px rgba(0, 0, 0, 0.15);
    font-size: 2.2rem;
    margin-bottom: .3rem;
    margin-top: 5rem;
}
.welcome-container img{
    width: 400px;
    height: 400px;
    color: white;
    font-size: 100px;
    font-family: Futura;
    background-color: #666666;
    box-shadow: 1px 4px 4px #555;
    text-align: center;
    border-radius: 7px;
}
.welcome-container section span::before,span::after,span{
    height: 0;
    background-color: transparent;
    font-family: 'signatureAlb0';
    font-size: 50px;
}

.welcome-container section{
    margin-top: 20px;
    color:white;
    background-color: transparent;
    font-family:cursive;
    width: 70%;
}

