.container1 {
    width: 450px;
    height: 300px;
    position: relative;
    border-radius: 5px;
  }
 
 .container1 img {
    width: 100%;
    height: 100%;
 }
 
 .container1:before {
   position: absolute;
   top: 0;
   left: 0;
   height: 100%;
   width: 100%;
   content: '';
   background-color: #2d3436;
   opacity: 0.8;
 }
 
 .caption {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: white;
    text-transform: uppercase;
 }
 
 .container1 .caption h2 {
    text-align: center;
    font-size: 17px;
    padding: 20px;
    margin-top: 20px;
    background: rgba(0, 0, 0, 0.8);
 }
 
 .container1 .caption p {
    font-size: 16px;
    width: 80%;
    align-self: center;
    text-align: center;
 }
 
 .container1 .caption a {
    text-decoration: none;
    background: #000;
    color: #fff;
    width: 89px;
    padding: 15px;
    margin-bottom: 20px;
    align-self: center;
 }
 
 /* OUR EFFECT STYLES GOES HERE */
 .container1:before {
   transform: translate(650px, -100px) rotate(180deg);
   transition: all 0.2s;
 }
 
 .container1:hover:before {
   transform: translate(0, 0) rotate(0deg);
 }
 
 .container1 .caption h2 {
   transform: translateY(-150px);
   transition: all 0.2s;
 }
 
 .container1:hover .caption h2 {
   transform: translateY(0px);
   transition-delay: 0.5s;
 }
 
 
 .container1 .caption p {
   transform: translateX(300px) rotate(90deg);
   transition: all 0.2s;
 }
 
 .container1:hover .caption p {
   transform: translateX(0) rotate(0deg);
   transition-delay: 0.4s;
 }
 
 .container1 .caption a {
   transform: translateY(-300px);
   transition: all 0.2s;
 }
 
 .container1:hover .caption a {
   transform: translateY(0px);
   transition-delay: 0.3s;
 }
 
 .container1 {
   overflow: hidden;
 }