.btn {
   border: 3px solid black;
   background:none;
   color: #87b83d;
   padding: 20px 40px;
   font-size: 20px;
   text-transform: uppercase;
   cursor: pointer;
   transition: all 0.5s;
   position: relative;
   color: white   ;
   overflow: hidden;
   letter-spacing: 2px;
 }
 
 .btn:hover {
   color: black;
 }
 
 .btn:before {
   content: "";
   position: absolute;
   bottom: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background: #2876a5;
   z-index: -1;
   border-radius: 50% 50% 0% 0%;
   height: 0%;
   transition: all 0.5s;
 }
 
 .btn:hover:before {
   height: 190%;
 }