.buttonidea4 {
	text-decoration: none;
	color: white;
	font-family: sans-serif;
	font-size: 20px;
	border: 3px solid black;
	padding: 20px 40px;
	position: relative;
	transition: all 1s;
	overflow: hidden;
	background-color: #2876a5;
	letter-spacing: 2px;
}

.buttonidea4:before {
	content: 'CLICK!';
	color: black;
	background-color: #87b83d;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	left: 0;
	top: -3px;
	height: 100%;
	width: 100%;
	transform: translateY(-100%);
	transition:all .5s;
}

.buttonidea4:hover:before {
	transform: translateY(3px);
}