.button-idea1 {
    padding: 20px 40px;
    font-size: 20px;
    font-family: sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    background-color: #2876a5 ;
    color: white;
    letter-spacing: 2px;
    position: relative;
    border: 3px solid black;
  }
  
  .button-idea1:before {
    content: "BUTTON";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #f1c40f;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: rotateX(270deg);
    transform-origin: top;
    transition: all 0.5s;
  }
  
  .button-idea1:after {
    content: "BUTTON";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #87b83d;
    display: flex;
    justify-content: center;
    align-items: center;
    transform-origin: bottom;
    transform: rotateX(270deg);
    transition: all 0.5s;
    transition-delay: 0.25s;
  }
  
  .button-idea1:hover:before, .button-idea1:hover:after {
     transform: rotateX(0deg); 
  }